
import qs from "qs";
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
directives: {
TransferDom
},
components: {
InlineXSwitch,
Confirm
},
data() {
return {
linkmanName: "",
phone: "",
email: "",
isDef: 0,
isDefault: false,
id: "",
showCofirm: false,
};
},
methods: {
checkPlayerPhone() {},
saveDate() {
if (this.isDefault) {
this.isDef = 0;
} else {
this.isDef = 1;
}
let params = {
id:this.id,
linkman: this.linkmanName,
phone: this.phone,
email: this.email,
isDef: this.isDef
};
this.$axios.post("/api/linkman/edit", qs.stringify(params)).then(res => {
if (res.data.code == 0) {

this.$router.push({ name: "commonInformation" });
}
});
},
onConfirm() {
let params = {
id: this.id
};
this.$axios.get("/api/linkman/del", { params }).then(res => {
if (res.data.code == 0) {

this.$router.push({ name: "commonInformation" });
}
});
},
delBtn(){
this.showCofirm = true;
},
onCancel() {
this.showCofirm = false;
},
cancleBtn() {

this.$router.push({ name: "commonInformation" });
}
},
created() {
let obj = JSON.parse(sessionStorage.getItem("editCurrentLinkmanInfo"));
let id = obj.id;
let params = {
id: id
};
this.$axios.get("/api/linkman/get_by_id", { params }).then(res => {
console.log(res);
if (res.data.code == 0) {
let rst = res.data.data;
this.linkmanName = rst.linkman;
this.phone = rst.phone;
this.email = rst.email;
this.isDef = rst.isDef;
this.id = rst.id;
if (rst.isDef == 0) {
this.isDefault = true;
} else {
this.isDefault = false;
}
}
});
}
};



